
.my-search {
.search-box-wrapper {
    margin: 20px;
}
}
.search-box-wrapper {
	margin: 20px;
}
.my-search-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 100%;
    padding: 0 6px;
    height: 40px;
    background: #333;
    border-radius: 6px
}
.my-search-box .box {
	-webkit-box-flex: 1;
	-ms-flex: 1;
	flex: 1;
    margin: 0 5px;
    line-height: 18px;
    border: 0;
    outline: none;
    background: #333;
    color: #fff;
    font-size: 14px;
}
.my-search .shortcut-wrapper {
    position: fixed;
    top: 178px;
    bottom: 0;
    width: 100%;
}
.my-search .shortcut-wrapper .shortcut .hot-key {
    margin: 0 20px 20px;
}
.shortcut-wrapper .shortcut .hot-key .title {
    margin-bottom: 20px;
    font-size: 14px;
    color: hsla(0,0%,100%,.5);
}
.my-search .shortcut-wrapper .shortcut .hot-key h1 {
    display: block;
    font-size: 0.9em;
    -webkit-margin-before: 0.67em;
    -webkit-margin-after: 0.67em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
    font-weight: bold;
}
ul, menu, dir {
    display: block;
    list-style-type: disc;
    -webkit-margin-before: 1em;
    -webkit-margin-after: 1em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
}
.my-search .shortcut-wrapper .shortcut .hot-key .item {
    display: inline-block;
    padding: 5px 10px;
    margin: 0 20px 10px 0;
    border-radius: 6px;
    background: #333;
    font-size: 14px;
    color: hsla(0,0%,100%,.3);
}
.my-search-box .icon-search, .my-search-box .icon-dismiss {
    font-size: 16px;
    color: #222;
}



