
.login_box {
	padding-top: 130px;
	height: 550px;
}
.login_box input {
	padding-top: 4px;
	padding-bottom: 4px;
	margin-bottom: 20px;
	margin-top: 8px;
	display: block;
	border: 1px solid #5b5b5b !important;
	color: #cccccc !important;
	border-radius: 5px;
	background: #383838;
	height: 30px;
}
.login_box input,
.login_box label {
	width: 100%;
}
.login_box label p {
	font-weight: normal;
}
.login_box .highlight_btu {
	background: #e4e4e4;
	color: black !important;
}
.login_box .highlight_border {
	border: 1px solid white !important;
}




